<template lang="pug">
b-container#register
  b-row
    b-col(cols='12')
      h1.text-center 
    b-col(cols='12')
      b-form(@submit.prevent="register")
        b-form-group(label='' label-for='input-account' description=' 4  20 ' :state='state.account' invalid-feedback="")
          b-form-input#input-account(v-model="form.account" required placeholder='' type="text" :state='state.account')
        b-form-group(label='' label-for='input-password' description=' 4  20 ' :state='state.password' invalid-feedback="")
          b-form-input#input-account(v-model="form.password" required placeholder='' type="password" :state='state.password')
        b-form-group(label='' label-for='input-email' description='' :state="state.email" invalid-feedback="")
          b-form-input#input-account(v-model="form.email" required placeholder='' type="text" :state="state.email")
        .text-center
          b-btn.mx-1(variant='success' type='sumbit') 
</template>

<script>
import validator from 'validator'

export default {
  data () {
    return {
      form: {
        account: '',
        password: '',
        email: ''
      }
    }
  },
  computed: {
    state () {
      return {
        account: this.form.account.length === 0 ? null : this.form.account.length >= 4 && this.form.account.length <= 20,
        password: this.form.password.length === 0 ? null : this.form.password.length >= 4 && this.form.password.length <= 20,
        email: this.form.email.length === 0 ? null : validator.isEmail(this.form.email)
      }
    }
  },
  methods: {
    async register () {
      try {
        await this.api.post('/users', this.form)
        this.$swal({
          icon: 'success',
          title: '',
          text: ''
        })
        this.$router.push('/')
      } catch (error) {
        console.log(error)
        this.$swal({
          icon: 'error',
          title: '',
          text: error.response.data.message
        })
      }
    }
  }
}
</script>
